export default {
	name: "index-list",
	props: {
		item: Object,
		index: Number
	},
	data() {
		return{
			
		}
	},
	onLoad() {
		console.log(this.listsData);
	},
	methods: {
		//点击关注事件
		async guanzhu() {
			let [err,res] = await this.$http.post('/follow',{
				follow_id: this.item.userid
			},{
				token: true,
				checkToken:true,
				checkAuth: true
			});
			//错误处理
			if(!this.$http.errorCheck(err,res)) return;
			//修改数据
			uni.showToast({ title: '关注成功' });
			let resData = {
				type: 'guanzhu',
				userid:this.item.userid,
				data: true
			}
			// 通知父组件
			this.$emit('changeevent',resData);
			// 通知全局修改数据
			uni.$emit('updateData',resData);
		},
		//顶踩事件
		async caozuo(type) {
			let index = (type == 'ding') ? 1 : 2;  // 操作后的状态
			if(this.item.infonum.index == index) return; // 状态相同不修改
			let [err,res] = await this.$http.post('/support',{
				  post_id: this.item.id,
				  type: index-1
			},{
				token: true,
				checkToken: true,
				checkAuth: true
			});
			// 错误处理
			if (!this.$http.errorCheck(err,res)) return;
			uni.showToast({
				title: index == 1 ? "顶成功" : "踩成功"
			});
			let resDate = {
				type: 'support',
				post_id: this.item.id,
				do: type 
			};
			// 通知父组件
			this.$emit('changeevent',resDate);
			// 通知全局
			uni.$emit("updateData",resDate);
		},
		//打开用户空间
		openSpace(){
			uni.navigateTo({
				url: '/pages/user-space/user-space?userid='+this.item.userid
			})
		},
		//进入详情页
		opendetail() {
			uni.navigateTo({
				url: '../../pages/detail/detail?detailData='+JSON.stringify(this.item),
			});
		}
	}
}
